<template>
  <!--slider-->
  <header>
    <div id="carouselExampleIndicators" class="carousel slide" data-ride="carousel">
      <div class="carousel-inner" role="listbox">
        <!-- Slide One - Set the background image for this slide in the line below -->
        <div class="carousel-item" v-for="(slider,i) in sliders" :key="i" :class="{ 'active': 0 === i }">
          <img :src="'http://localhost/Rpi/public/'+slider.slider_image" alt="" width="100%">
          <div class="carousel-caption d-none d-md-block">
            <h3>First Slide</h3>
            <p>This is a description for the first slide.</p>
          </div>
        </div>

      </div>
      <a class="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
        <span class="sr-only">Previous</span>
      </a>
      <a class="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
        <span class="carousel-control-next-icon" aria-hidden="true"></span>
        <span class="sr-only">Next</span>
      </a>
    </div>
  </header>
</template>

<script>
export default {
  data () {
    return {
      sliders: ''
    }
  },
  name: 'Slider',
  methods: {
    clickMe: function () {
      this.$http.get('http://localhost/Rpi/public/api/slider').then(response => {
        this.sliders = response.body
      }, response => {
      })
    }
  },
  created () {
    this.clickMe()
  }
}
</script>

<style scoped>

</style>



